* {
    padding: 0;
    margin: 0;
    font-family: fot-tsukuardgothic-std, sans-serif;
}

.app {
    background-color: #F2F0E9;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;

    .appWrapper {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 70%;
        align-items: center;

        .title {
            height: 80px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .inputmemo {
            height: 20px;
            width: 100%;
            display: flex;
            align-items: flex-end;
            justify-content: end;

            .modal-open {
                border-radius: 10px;
                width: 30px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;

                &:hover {
                    background-color: #e7e1c3;
                }
            }

            .modal {
                visibility: hidden;
                height: 100vh;
                width: 100%;
                position: absolute;
                top: 0;
                left: 0;
                display: flex;
                justify-content: center;
                align-items: center;

                .modal-inner {
                    z-index: 1;
                    height: 60%;
                    width: 40%;
                    background-color: white;

                    .modal-close {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        height: 40px;
                        background-color: #F2F0E9;

                        .title {
                            height: 40px;
                            display: flex;
                            justify-content: start;
                            align-items: center;
                            padding-left: 20px;
                        }

                        .modal-icon {
                            cursor: pointer;
                            width: 40px;
                            height: 40px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            flex-direction: row-reverse;

                            &:hover {
                                background-color: #e7e1c3;
                            }

                        }
                    }

                    .modal-form {
                        width: 100%;
                        height: 100%;

                        form {
                            margin-top: 20px;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            gap: 15px;
                            width: 100%;

                            input {
                                height: 30px;
                                width: 80%;
                                border-radius: 10px;
                                padding: 5px;
                                outline: none;
                            }

                            textarea {
                                padding: 5px;
                                height: 150px;
                                width: 80%;
                                border-radius: 10px;
                            }

                            button {
                                padding: 5px;
                                height: 30px;
                                width: 80%;
                                border-radius: 10px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                            }
                        }
                    }
                }



                .backGround {
                    background-color: rgb(105, 94, 94);
                    height: 100vh;
                    width: 100%;
                    position: absolute;
                    opacity: 0.3;
                    cursor: pointer;
                }

                &.isshow {
                    visibility: visible;
                }
            }

        }

        .memolist {
            height: calc(100% - 90px);
            width: 100%;
            margin-bottom: 10px;
            overflow-y: scroll;

            p {
                display: flex;
                justify-content: center;
                align-items: center;
            }

            &::-webkit-scrollbar {
                width: 5px;
            }

            &::-webkit-scrollbar-track {
                background-color: transparent;
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 5px;
                background-color: gray;
            }

            .memocaption {
                display: flex;
                align-items: center;
                justify-content: center;
                border-top: 1px solid black;
                border-left: 1px solid black;
                border-right: 1px solid black;

                &:last-child {
                    border-bottom: 1px solid black;
                }

                .deployment {
                    flex: 5;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 100%;
                    border-right: 1px solid black;
                    padding: 7px;
                }


                .title {
                    flex: 20;
                    border-right: 1px solid black;
                    height: 100%;
                    display: flex;
                    padding: 10px 5px;


                    p {
                        width: 100%;
                        overflow: hidden;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                    }

                }

                .description {
                    flex: 40;
                    height: 100%;
                    border-right: 1px solid black;
                    padding: 10px 5px;

                    p {
                        width: 100%;
                        overflow: hidden;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                    }
                }

                .creationdate {
                    flex: 30;
                    height: 100%;
                    border-right: 1px solid black;
                    padding: 10px 0px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    p {
                        width: 100%;
                        overflow: hidden;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                }

                .delete {
                    height: 100%;
                    width: 100%;
                    padding: 10px 0px;
                    flex: 5;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;

                    &:hover {
                        background-color: rgb(250, 162, 162);
                    }


                    .icon {
                        height: 100%;
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                }




                .memo {
                    .modal-open {
                        border-radius: 10px;
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;

                        &:hover {
                            background-color: #e7e1c3;
                        }
                    }

                    .modal {
                        visibility: hidden;
                        height: 100vh;
                        width: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        display: flex;
                        justify-content: center;
                        align-items: center;


                        .modal-inner {
                            z-index: 1;
                            height: 60%;
                            width: 40%;
                            background-color: white;

                            .modal-close {
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                                height: 40px;
                                background-color: #F2F0E9;

                                .title {
                                    height: 40px;
                                    display: flex;
                                    justify-content: space-between;
                                    align-items: center;
                                    padding-left: 20px;
                                    border: none;

                                    span {
                                        font-size: 13px;
                                    }
                                }

                                .modal-icon {
                                    cursor: pointer;
                                    width: 40px;
                                    height: 40px;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    flex-direction: row-reverse;

                                    &:hover {
                                        background-color: #e7e1c3;
                                    }

                                }
                            }

                            .modal-form {
                                width: 100%;
                                height: 100%;

                                form {
                                    margin-top: 10px;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: center;
                                    align-items: center;
                                    gap: 15px;
                                    width: 100%;

                                    input {
                                        height: 30px;
                                        width: 80%;
                                        border-radius: 10px;
                                        padding: 5px;
                                        outline: none;
                                    }

                                    textarea {
                                        padding: 5px;
                                        height: 150px;
                                        width: 80%;
                                        border-radius: 10px;
                                    }

                                    button {
                                        padding: 5px;
                                        height: 30px;
                                        width: 80%;
                                        border-radius: 10px;
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                    }
                                }
                            }
                        }



                        .backGround {
                            background-color: rgb(105, 94, 94);
                            height: 100vh;
                            width: 100%;
                            position: absolute;
                            opacity: 0.3;
                            cursor: pointer;
                        }

                        &.isshow {
                            visibility: visible;
                        }
                    }
                }
            }
        }
    }

}